<template>
	<uni-popup ref="popup" type="bottom">
		<view class="popup-content">
			<view class="popup-header">
				<text class="popup-title">物品信息</text>
				<view class="popup-close" @tap="close">
					<uni-icons type="closeempty" size="22" color="#666666"></uni-icons>
				</view>
			</view>
			
			<scroll-view scroll-y class="popup-scroll">
				<view class="item-type-section">
					<view class="section-header">
						<text class="section-title">物品类型</text>
						<text class="weight-tip" @tap="showWeightTip">哪些物品不能寄？</text>
					</view>
					<input 
						class="item-input" 
						type="text" 
						v-model="itemDetail"
						placeholder="请输入物品名称"
						placeholder-class="input-placeholder"
					/>
					<view class="type-list">
						<view 
							class="type-item" 
							:class="{active: selectedType === '日用品'}" 
							@tap="selectType('日用品')"
						>
							<text>日用品</text>
						</view>
						<view 
							class="type-item" 
							:class="{active: selectedType === '文件'}" 
							@tap="selectType('文件')"
						>
							<text>文件</text>
						</view>
						<view 
							class="type-item" 
							:class="{active: selectedType === '食品'}" 
							@tap="selectType('食品')"
						>
							<text>食品</text>
						</view>
					</view>
				</view>
				
				<view class="item-divider"></view>
				
				<view class="item-weight-section">
					<view class="weight-header">
						<text class="section-title">预估重量</text>
						<text class="weight-tip" @tap="showWeightTip">物品如何计费？</text>
					</view>
					<view class="weight-input-wrapper">
						<view class="weight-input">
							<text class="minus" @tap="decreaseWeight">-</text>
							<input type="number" v-model="weight" class="weight-value" />
							<text class="unit">kg</text>
							<text class="plus" @tap="increaseWeight">+</text>
						</view>
					</view>
				</view>
				
				<view class="item-divider"></view>
				
				<view class="remark-section">
					<text class="section-title">上门备注</text>
					<textarea 
						class="remark-input" 
						v-model="remark" 
						placeholder="请输上门备注信息" 
						placeholder-class="input-placeholder"
						maxlength="100" 
					/>
				</view>
			</scroll-view>
			
			<view class="popup-footer">
				<button 
					class="confirm-btn" 
					@tap="confirm"
					:class="{ 'confirm-btn-active': canConfirm }"
					:disabled="!canConfirm"
				>确定</button>
			</view>
		</view>
	</uni-popup>
</template>

<script>
export default {
	name: 'item-popup',
	data() {
		return {
			selectedType: '',
			weight: 1,
			remark: '',
			itemDetail: ''
		}
	},
	computed: {
			canConfirm() {
				return this.itemDetail.trim() !== '' || this.selectedType !== ''
			}
	},
	methods: {
		open() {
			this.$refs.popup.open()
		},
		close() {
			this.$refs.popup.close()
		},
		decreaseWeight() {
			if (this.weight > 0.5) {
				this.weight -= 0.5
			}
		},
		increaseWeight() {
			this.weight += 0.5
		},
		showWeightTip() {
			// 显示不可寄物品提示
		},
		selectType(type) {
			this.selectedType = type
			this.itemDetail = type
		},
		confirm() {
			if (!this.canConfirm) return
			
			this.$emit('confirm', {
				itemDetail: this.itemDetail,
				weight: this.weight,
				remark: this.remark
			})
			this.close()
		}
	}
}
</script>

<style>
.popup-content {
	background-color: #FFFFFF;
	border-radius: 24rpx 24rpx 0 0;
	position: relative;
	z-index: 999;
	height: 65vh;
	display: flex;
	flex-direction: column;
}

.popup-header {
	padding: 30rpx;
	display: flex;
	justify-content: space-between;
	align-items: center;
	border-bottom: 1rpx solid #F5F5F5;
}

.popup-title {
	font-size: 34rpx;
	font-weight: bold;
	color: #333333;
}

.popup-close {
	padding: 10rpx;
}

.popup-scroll {
	flex: 1;
	overflow-y: auto;
	-webkit-overflow-scrolling: touch;
}

.item-type-section {
	padding: 30rpx;
	border-bottom: 20rpx solid #F5F5F5;
}

.section-header {
	display: flex;
	justify-content: space-between;
	align-items: center;
	margin-bottom: 24rpx;
	padding-right: 20rpx;
}

.section-title {
	font-size: 32rpx;
	font-weight: bold;
	color: #333333;
}

.weight-tip {
	font-size: 26rpx;
	color: #007AFF;
	flex-shrink: 0;
}

.item-input {
	height: 80rpx;
	background-color: #F8F8F8;
	border-radius: 8rpx;
	padding: 0 24rpx;
	font-size: 28rpx;
	margin-bottom: 24rpx;
}

.type-list {
	display: flex;
	flex-wrap: wrap;
	gap: 24rpx;
	justify-content: space-between;
}

.type-item {
	width: calc((100% - 48rpx) / 3);
	height: 80rpx;
	border-radius: 8rpx;
	background-color: #F8F8F8;
	font-size: 30rpx;
	color: #333333;
	display: flex;
	align-items: center;
	justify-content: center;
	transition: all 0.3s;
}

.type-item.active {
	background-color: #E6F2FF;
	color: #007AFF;
	font-weight: bold;
}

.item-divider {
	height: 1rpx;
	background-color: #F5F5F5;
	margin: 0;
}

.item-weight-section {
	padding: 30rpx;
	border-bottom: 20rpx solid #F5F5F5;
}

.weight-header {
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding-right: 20rpx;
	margin-bottom: 24rpx;
}

.weight-input-wrapper {
	display: flex;
	justify-content: center;
}

.weight-input {
	display: flex;
	align-items: center;
	background-color: #F8F8F8;
	border-radius: 8rpx;
	padding: 0 12rpx;
	height: 80rpx;
}

.minus, .plus {
	width: 80rpx;
	height: 80rpx;
	display: flex;
	align-items: center;
	justify-content: center;
	font-size: 44rpx;
	color: #333333;
	font-weight: bold;
}

.weight-value {
	width: 140rpx;
	height: 80rpx;
	text-align: center;
	font-size: 36rpx;
	background-color: transparent;
	font-weight: bold;
}

.unit {
	font-size: 32rpx;
	color: #666666;
	padding: 0 16rpx;
	font-weight: 500;
}

.remark-section {
	padding: 30rpx;
	padding-bottom: 40rpx;
}

.remark-input {
	width: 100%;
	height: 160rpx;
	background-color: #F8F8F8;
	border-radius: 8rpx;
	padding: 24rpx;
	padding-right: 40rpx;
	font-size: 28rpx;
	margin-top: 24rpx;
	box-sizing: border-box;
}

.popup-footer {
	padding: 24rpx 30rpx calc(24rpx + env(safe-area-inset-bottom));
	border-top: 1rpx solid #F5F5F5;
	background-color: #FFFFFF;
}

.confirm-btn {
	background: linear-gradient(to right, #CCCCCC, #999999);
	font-size: 32rpx;
	font-weight: bold;
	height: 88rpx;
	line-height: 88rpx;
	border-radius: 44rpx;
	border: none;
	opacity: 0.6;
	pointer-events: none;
	transition: all 0.3s;
	color: #FFFFFF !important;
}

.confirm-btn-active {
	background: linear-gradient(to right, #FF9500, #FF5E3A);
	opacity: 1;
	pointer-events: auto;
}
</style> 